<template>
  <div>
    <!-- 面包屑导航区 -->
    <div class="page-header">
      <h3 class="page-title">
      <span class="page-title-icon bg-gradient-info text-white mr-2">
        <i class="mdi mdi-account"></i>
      </span> Change Password </h3>
    </div>
    <div class="row">
      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <h3>Change Password</h3>
            <!-- 改密码表单 -->
            <div class="form_center_layout">
              <el-form
                  ref="changePasswordFormRef"
                  :model="changePasswordForm"
                  :rules="changePasswordFormRules"
                  label-width="130px"
              >
                <el-form-item label="Old Password" prop="oldPass">
                  <el-input v-model="changePasswordForm.oldPass" type="password"
                            prefix-icon="iconfont icon-3702mima"></el-input>
                </el-form-item>
                <el-form-item label="Password" prop="pass">
                  <el-input
                      v-model="changePasswordForm.pass"
                      type="password"
                      prefix-icon="iconfont icon-3702mima"
                  ></el-input>
                </el-form-item>
                <el-form-item label="ConfirmPass" prop="confirmPass">
                  <el-input
                      v-model="changePasswordForm.confirmPass"
                      type="password"
                      prefix-icon="iconfont icon-3702mima"
                  ></el-input>
                </el-form-item>

                <b-button type="button" variant="info" @click="changePass">Submit</b-button>

              </el-form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please enter password'));
      } else {
        if (this.changePasswordForm.confirmPass !== '') {
          this.$refs.changePasswordFormRef.validateField('confirmPass');
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please enter password again'));
      } else if (value !== this.changePasswordForm.pass) {
        callback(new Error('The two passwords do not match!'));
      } else {
        callback();
      }
    };
    return {
      changePasswordForm: {
        oldPass: '',
        pass: '',
        confirmPass: '',
      },
      // 表单验证
      changePasswordFormRules: {
        oldPass: [
          {required: true, message: 'Please enter your old password', trigger: 'blur'},
          {min: 2, max: 18, message: 'The length of password should between 2 to 18 characters', trigger: 'blur'}
        ],
        pass: [
          {validator: validatePass, trigger: 'blur'},
          {required: true, message: 'Please enter new password', trigger: 'blur'},
          {min: 2, max: 18, message: 'The length of password should between 2 to 18 characters', trigger: 'blur'}
        ],
        confirmPass: [
          {validator: validatePass2, trigger: 'blur'},
          {required: true, message: 'Please enter password', trigger: 'blur'},
        ],
      }
    }
  },
  methods: {
    changePass() {
      // 表单预验证
      // valid：bool类型
      this.$refs.changePasswordFormRef.validate(async valid => {
        if (!valid) {
          return false;
        }
        this.$confirm('Do you really want to change your password?', 'Change Password', {
          confirmButtonText: 'Yes',
          cancelButtonText: 'No',
          cancelButtonClass: 'btn-custom-cancel',
          type: 'success'
        }).then(async () => {
          let changePasswordFormEncrypted = this.$lodash.clone(this.changePasswordForm);
          changePasswordFormEncrypted.pass = this.$jse.encrypt(this.changePasswordForm.pass);
          changePasswordFormEncrypted.confirmPass = this.$jse.encrypt(this.changePasswordForm.confirmPass);
          changePasswordFormEncrypted.oldPass = this.$jse.encrypt(this.changePasswordForm.oldPass);
          const info = await this.$axios.post('changePassword', changePasswordFormEncrypted);
          if (info) {
            this.$router.push("/login");
          }
        }).catch(() => {
        });
      })
    }
  }
}
</script>

<style scoped>
.el-card {
  min-height: 630px;
}
</style>
